<template>
  <div
    v-if="NowInfo"
    style="background-color:#f6f6f6;font-size:15px; padding:20px 0;position:fixed;top:50px;bottom:0px;left:0px;right:0px;overflow-y:scroll;"
  >
    <!-- 个人信息 -->
    <section>
      <div class="headUrl">
        <img
          v-if="NowInfo.order"
          :src="(NowInfo.order &&  NowInfo.order.headUrl) || (NowInfo.sysUser && NowInfo.sysUser.headUrl)"
          alt
        />
        <!-- src="https://wx.qlogo.cn/mmopen/vi_32/9fcFhCxx3sxFwOtNJTsfkn2Us0pDbFv4f88ThciaCRHoSnTjHqXM35USNwv7LLBZliceVvMic8wx9lQkDNNfWEDqA/132" -->
        <img
          v-else
          src="@/assets/hotelAudit/normal.png"
          alt
        />
        <div class="headUrl_fo">

          <div class="name">
            <p>{{ NowInfo.order && NowInfo.order.roomUser || $route.query.cooperationName || '暂无' }}</p>
            <p
              class="c_88 line"
              v-if="!$route.query.cooperOrderId"
            >{{ NowInfo.virtualRoomScale?NowInfo.virtualRoomScale.scaleName?NowInfo.virtualRoomScale.scaleName:'暂无':'暂无' }}-{{ NowInfo.roomSn }}</p>
            <p class="c_88 line">密码有效期至：
              <span
                v-if="NowInfo.order && NowInfo.order.payment > '0'"
                style="color:#000;font-weight:bold;"
              >{{ NowInfo.effectTime | dataFilter('YYYY.MM.DD') }}</span>
              <span v-else>暂无</span>
            </p>
          </div>
          <div
            @click="goDetail"
            class="img_right"
          >
            <span class="c_88">租赁详情</span>
            <img
              src="../../assets/Chevron.png"
              alt
            />
          </div>
        </div>
      </div>
    </section>
    <!-- 查询生活，房费账单 -->
    <div
      v-if="lifeInfo.length == 0"
      class="add_btn"
      @click="choose('生活账单')"
    >
      <van-icon
        name="add"
        color='#FF7F47'
        size='18px'
        style="padding-right:10px;"
      />
      <div>添加生活账单</div>
    </div>
    <section>
      <div id="roomBox">
        <div
          class="room_fo"
          v-for="(item,i) in allInfo"
          :key="i"
        >
          <div
            class="loop"
            :class="item.payStatus == 1?'payed':'nopay'"
          >
            <div
              class="loop_l"
              @click="goRoom(item)"
            >
              <div>
                <p style="white-space:nowrap;">
                  <span v-if="NowInfo.orderType == '3'">
                    {{ item.startTime | dataFilter('YYYY.MM.DD') }}~{{ item.showEndTime | dataFilter('YYYY.MM.DD') }}
                  </span>
                  <span v-if="NowInfo.orderType == '4'">
                    {{ item.new_endTime ? item.new_endTime : "" }}
                  </span>
                  <span
                    v-if="!item.set_life"
                    class="w16"
                    :class="item.payStatus == '1'?'ffa':'ff7'"
                  >租金</span>
                  <span
                    v-else
                    class="w16"
                    :class="item.payStatus == '1'?'edf':'bc'"
                  >生活</span>
                </p>
                <p
                  v-if="item.diff > '0' && item.payStatus == '0'"
                  class="f12"
                >距收款日：{{Math.abs(item.diff)}}天</p>
                <p
                  v-else-if="item.diff < '0' && item.payStatus == '0'"
                  class="c_f0 f12"
                >已逾期{{Math.abs(item.diff)}}天</p>
                <p
                  v-else-if="item.diff == '0' && item.payStatus == '0'"
                  class="f12"
                >今日到期</p>
                <p
                  v-else-if="item.payStatus == '1'"
                  class="f12"
                >到账日期：{{ item.payTime?item.payTime.substring(0,10):item.payTime }}</p>
              </div>
              <img
                v-if="item.payStatus == '1'"
                class="ispayed"
                src="@/assets/order/payed.png"
                alt
              />
            </div>
            <p
              class="money"
              v-if="item.set_life"
              @click="paySetting(item)"
            >
                {{item.status == 0 && item.set_round == 0 && $route.query.cooperOrderId?'费用设置':'￥'+item.totalPrice}}
            </p>
            <p
              class="money"
              v-else
            >&yen;{{item.roomPrice}}</p>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>
<script>
import dayjs from 'dayjs';
import { GetBusinessCooperOrder, GetBusinessCooperRentalBillBycooperorderroom, GetBusinessCooperLifeCostBillBycooperorderroom, GetBusinessCooperRenewalContractLastByCooperOrderRoom } from '@/api/api_business';
import { GetBusinessOrderroom, GetBusinessLifeCostBillByorderroom, GetBusinessRentalBillByorderroom } from '@/api/api_business_hotel_and_scale';
export default {
    data() {
        return {
            NowInfo: [],
            rentInfo: {},
            roomInfo: [],
            lifeInfo: [],
            allInfo: [],
            lastEndTime: ''
        };
    },
    filters: {
        dataFormat(val, type = false) {
            let str = type ? dayjs(val).format('YYYY年MM月DD') : dayjs(val).format('YYYY-MM-DD');
            return str;
        }
    },
    created() {
        this.$store.commit('setHeader', '租赁信息');
        if (this.$route.query.orderRoomId) {
            this.getCurrentInfo(this.$route.query.orderRoomId);
        } else {
            this.getCooperDetails(this.$route.query.cooperOrderId);
            this.getCooperBill(this.$route.query.cooperOrderId);
        }
    },
    methods: {
        // 根据id 获取单位订单详情
        getCooperDetails(id) {
            GetBusinessCooperOrder({ cooperOrderId: id }).done((res) => {
                this.getCooperLast(id);
                this.NowInfo = res.data;
            });
        },
        // 根据单位订单查询最后一次合同
        getCooperLast(id) {
            GetBusinessCooperRenewalContractLastByCooperOrderRoom({ cooperOrderRoomId: id }).done((res) => {});
        },
        //获取单位生活&房费账单
        getCooperBill(id) {
            let list = [GetBusinessCooperRentalBillBycooperorderroom({ cooperOrderRoomId: id }), GetBusinessCooperLifeCostBillBycooperorderroom({ cooperOrderRoomId: id })];
            Promise.all(list)
                .then((res) => {
                    let lifeAll = [];
                    let roomAll = [];
                    res.map((item, i) => {
                        if (item.ret == '1' && i == '0') {
                            roomAll = item.data;
                        } else if (item.ret == '1' && i == '1') {
                            lifeAll = item.data;
                        }
                    });
                    let _roomList = [];
                    let _lifeList = [];
                    if (roomAll.length > 0) {
                        _roomList = roomAll.map((element, i) => {
                            let obj = {
                                new_endTime: this.lastDay(element.startTime),
                                diff: this.difffDay(this.lastDay(element.startTime)),
                                set_round: i,
                                set_length: roomAll.length,
                                showEndTime: this.lastDay(element.endTime)
                            };
                            Object.assign(element, obj);
                            return element;
                        });
                    }
                    if (lifeAll.length > 0) {
                        _lifeList = lifeAll.map((element, i) => {
                            let obj = {
                                diff: this.difffDay(element.endTime),
                                new_endTime: element.endTime,
                                set_round: i,
                                set_life: true,
                                set_length: lifeAll.length,
                                showEndTime: this.lastDay(element.endTime)
                            };
                            Object.assign(element, obj);
                            return element;
                        });
                    }
                    this.allInfo = _lifeList;
                })
                .catch((res) => {
                    console.log('err', res);
                });
        },
        // 当前信息
        getCurrentInfo(id) {
            GetBusinessOrderroom(id).done((res) => {
                this.NowInfo = res.data;
                this.getinfo_life(id);
            });
        },
        // 查询生活
        getinfo_life(id) {
            GetBusinessLifeCostBillByorderroom(id).done((res) => {
                // this.lifeInfo = res.data;
                // this.getinfo_room(id);
                // for (let i = 0; i < this.lifeInfo.length; i++) {
                //     let obj = {
                //         diff: this.difffDay(this.lifeInfo[i].endTime),
                //         new_endTime: this.lifeInfo[i].endTime,
                //         set_round: i,
                //         set_life: true,
                //         set_length: this.lifeInfo.length,
                //         showEndTime: this.lastDay(this.lifeInfo[i].endTime)
                //     };
                //     Object.assign(this.lifeInfo[i], obj);
                // }
                this.lifeInfo = res.data.map((item, index) => ({
                    ...item,
                    diff: this.difffDay(item.endTime),
                    new_endTime: item.endTime,
                    set_round: index,
                    set_life: true,
                    set_length: res.data.length,
                    showEndTime: this.lastDay(item.endTime)
                }));

                this.getinfo_room(id);
            });
        },
        // 房费账单
        getinfo_room(id) {
            GetBusinessRentalBillByorderroom(id).done((res) => {
                this.roomInfo = res.data;
                if (this.roomInfo.length == 0) {
                    return;
                }
                for (let i = 0; i < this.roomInfo.length; i++) {
                    if (i == 0) {
                        this.roomInfo[i].first_room = true;
                    } else {
                        this.roomInfo[i].first_room = false;
                    }
                    this.roomInfo[i].new_endTime = this.lastDay(this.roomInfo[i].startTime);
                    this.roomInfo[i].diff = this.difffDay(this.roomInfo[i].new_endTime);
                    this.roomInfo[i].showEndTime = this.lastDay(this.roomInfo[i].endTime);
                }
                this.allInfo = this.roomInfo.concat(this.lifeInfo);
                let payStatusAll = [];
                let noPayStatusAll = [];
                let dateAll = [];
                this.allInfo.map((item) => {
                    if (item.payStatus == '1') {
                        payStatusAll.push(item);
                    } else {
                        noPayStatusAll.push(item);
                    }
                });
                payStatusAll.sort((a, b) => {
                    return Date.parse(b.new_endTime) - Date.parse(a.new_endTime);
                });
                noPayStatusAll.sort((a, b) => {
                    return b.diff > a.diff ? 1 : -1;
                });
                dateAll = noPayStatusAll.concat(payStatusAll);
                this.allInfo = dateAll;
            });
        },
        // 房费账单前一天
        lastDay(time) {
            const days = dayjs();
            let last_sTime = dayjs(time).subtract(1, 'day');
            return last_sTime.format('YYYY-MM-DD');
        },
        // 相差天数
        difffDay(time) {
            const days = dayjs();
            let nowTime = days.format('YYYY-MM-DD');
            let diff = dayjs(time).diff(dayjs(nowTime), 'day');
            return diff;
        },
        // 订单详情
        goDetail() {
            this.$router.push({
                path: '/details',
                query: {
                    orderRoomId: this.$route.query.cooperOrderId ? '' : this.NowInfo.id,
                    cooperOrderId: this.$route.query.cooperOrderId,
                    cooperationName: this.NowInfo.order.roomUser
                }
            });
        },
        // 房费/生活账单
        goRoom(item) {
            if (!item.set_life) {
                if (!this.$route.query.cooperOrderId) {
                    this.$router.push({
                        path: '/roomFeeD',
                        query: {
                            rentalBillId: item.id,
                            orderType: this.NowInfo.orderType
                        }
                    });
                    localStorage.setItem('isFirst_room', item.first_room);
                } else {
                    this.$router.push({
                        path: '/business-room-details',
                        query: {
                            cooperOrderId: this.$route.query.cooperOrderId,
                            cooperRentalBillId: item.id,
                            set_length: item.set_length,
                            set_round: item.set_round
                        }
                    });
                }
            } else {
                localStorage.removeItem('life_fee');
                this.$router.push({
                    path: '/lifeFeetrans',
                    query: {
                        cooperOrderId: this.$route.query.cooperOrderId,
                        lifeCostBillId: item.id,
                        set_length: item.set_length,
                        set_round: item.set_round
                    }
                });
            }
        },
        // 单位订单首期费用设置
        paySetting(item) {
            if (item.status == 0 && item.set_round == 0 && this.$route.query.cooperOrderId) {
                this.$router.push({
                    path: '/business-pay-set',
                    query: {
                        cooperOrderId: item.cooperOrderId,
                        cooperLifeCostBillId: item.id
                    }
                });
            }
        },
        // 请选择账单类型
        choose(type) {
            if (this.$route.query.cooperOrderId) {
                this.$router.push({
                    path: '/add_bill_two',
                    query: {
                        title: type,
                        orderId: this.NowInfo.orderId,
                        orderRoomId: this.NowInfo.id,
                        startTime: this.NowInfo.order.startTime,
                        endTime: this.NowInfo.order.endTime
                    }
                });
            } else {
                this.$router.push({
                    path: '/add_bill',
                    query: {
                        title: type,
                        orderId: this.NowInfo.orderId,
                        orderRoomId: this.NowInfo.id,
                        startTime: this.NowInfo.order.startTime,
                        endTime: this.NowInfo.order.endTime
                    }
                });
            }
        }
    }
};
</script>

<style scoped>
.c_88 {
    color: #888;
    font-weight: normal;
}
.line {
    line-height: 18px;
}
.c_f0 {
    color: #f00;
}
.f12 {
    font-size: 0.6rem;
}
.w16 {
    display: inline-block;
    width: 48px;
    font-size: 0.6rem;
    text-align: center;
    color: #fff;
    border-radius: 5px;
    margin-left: 6px;
    line-height: 18px;
}
.payed {
    color: #888;
}
.headUrl {
    display: flex;
    padding: 10px 14px;
    background: #fff;
    border-bottom: 1px solid #f0f0f0;
    margin-bottom: 20px;
}
.headUrl > img {
    width: 35px;
    height: 35px;
    margin-right: 14px;
    border-radius: 20px;
}
.headUrl > .headUrl_fo {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}
.headUrl > .headUrl_fo > .name {
    text-align: left;
    font-size: 0.7rem;
    color: #000;
    font-weight: bold;
    width: 65%;
}
.headUrl > .headUrl_fo > .img_right {
    text-align: right;
    line-height: 20px;
    display: flex;
    align-items: center;
}
.headUrl > .headUrl_fo > .img_right > img {
    height: 13px;
    padding-left: 5px;
}
#roomBox {
    margin: 6px;
}
#roomBox > .room_fo > .loop {
    display: flex;
    justify-content: space-between;
    text-align: left;
    font-size: 0.8rem;
    align-items: center;
    line-height: 22px;
    margin: 10px 0;
    background: #fff;
    padding: 4px 8px;
    border-radius: 5px;
    height: 50px;
}
#roomBox > .room_fo > .loop > .money {
    min-width: 20%;
    text-align: right;
}
.loop_l {
    height: 100%;
    width: 90%;
    display: flex;
    justify-content: space-between;
}
.ispayed {
    height: 80%;
    margin: auto 0;
}
.ffa {
    border: 1px solid #ffab84;
    color: #ffab84;
}
.ff7 {
    border: 1px solid #ff7d41;
    color: #ff7d41;
}
.edf {
    border: 1px solid #8edfbc;
    color: #8edfbc;
}
.bc {
    border: 1px solid #02bc6c;
    color: #02bc6c;
}
.add_btn {
    display: flex;
    justify-content: center;
    align-items: center;
    color: #ff7f47;
    border-radius: 4px;
    background: #fff;
    line-height: 50px;
    margin: 0 6px;
}
.type_bottom {
    line-height: 40px;
    color: #000;
    border-bottom: 1px solid #efeff4;
}
.bb {
    border-bottom: 1px solid #efeff4;
}
</style>



